@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@300;600&display=swap');

/*Обнуление*/
*{padding:0;margin:0;border:0;}
*,*:before,*:after{-moz-box-sizing:border-box;-webkit-box-sizing:border-box;box-sizing:border-box;}
:focus,:active{outline:none;}
a:focus,a:active{outline:none;}
nav,footer,header,aside{display:block;}
html,body{box-sizing: border-box; height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%;}
input,button,textarea{font-family:inherit;}
input::-ms-clear{display:none;}
button{cursor:pointer;}
button::-moz-focus-inner{padding:0;border:0;}
a,a:visited{text-decoration:none;}
a:hover{text-decoration:none;}
ul li{list-style:none;}
img{vertical-align:top;}
h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:inherit;}
/*--------------------*/


body{
    font-family: 'Montserrat Alternates', sans-serif;
    font-weight: 300;
    background: #333;
    color: #fff;
    line-height: 20px;
}

.header{
    background: #000;
    padding: 20px;
    font-size: 20px;
    font-weight: 600;
}
main{
    width: 50%;
    float: left;
    padding: 50px;
}
.Users__h3{
    font-weight: 600;
}
main .user{
    margin-bottom: 50px;
    background: #3e8fa0;
    padding: 20px;
}
main .delete-icon{
    float: right;
    color: #c73b3b;
    cursor: pointer;
    transform: scale(2);
}
main .edit-icon{
    float: right;
    margin-right: 25px;
    color: #165664;
    cursor: pointer;
    transform: scale(2);
}
main .delete-icon:hover, main .edit-icon:hover{
    opacity: 0.5;
}

form input:not(input[id='isHappy']),
form textarea,
form button{
    width: 100%;
    font-family: 'Montserrat Alternates', sans-serif;
    font-weight: 300;
    background: #111111;
    color: #fff;
    padding: 15px 15px;
    font-size: 15px;
}
form input[id='isHappy']{
    margin: 15px;
}
form > *:not(:last-child){
    margin-bottom: 10px;
}

aside{
    width: 20%;
    float: right;
    margin: 50px 100px 50px 50px;
}

.user form{
    padding-top: 20px
}
.user form input:not(input[id='isHappy']),
.user form textarea,
.user form button{
    background: #134b57;
    
}